export default {
	name: 'Confirm',
	data() {
		return {
			show: false,
		};
	},
	props: {
		isShow: {
			type: Boolean,
			default: false
		}
	},
	watch: {
		isShow(nval, oval) {
			this.show = nval;
		}
	},
	created() {
		this.show = this.isShow;
	},
	methods: {
		reset() {
			this.show = false;
			this.$emit('noshow', this.show);
		},
		//登录
		getUserInfo() {
			let app={
				appid:'wx8e0e2a88e974b63c',
				secret:'01c6b5501d6b3a67bfcd25ebcc9e60f2'
			}
			uni.login({
				provider: 'weixin',
				success: loginRes=> {
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: infoRes=> {
							//用户数据
							this.$emit('userinfo',JSON.stringify(infoRes.userInfo))
							this.show = false;
							this.$emit('noshow', this.show);
							uni.showToast({
								title:'登录成功',
								icon:'none'
							})
							uni.request({
								url:'https://api.weixin.qq.com/sns/jscode2session?appid='+app.appid+'&secret='+app.secret+'&js_code='+loginRes.code+'&grant_type=authorization_code',
								data: {},
								method:'GET',
								success:res=>{
									//用户登录凭证
									this.$emit('openid',res.data.openid)
								}
							})
						}
					});
				}
			});
		}
	}
};
